.container{
    margin: 0 auto;
    padding: 0 15px;
    @media screen and (min-width: 480px) {
        max-width: 480px;
      }
      @media screen and (min-width: 768px) {
        max-width: 768px;
      }
      @media screen and (min-width: 1024px) {
        max-width: 1024px;
      }
      @media screen and (min-width: 1200px) {
        max-width: 1170px;
      }
}